'use client';

import { useState } from 'react';
import Image from 'next/image';

// Types for our emotion analysis response
interface EmotionResponse {
  emotion: string;
  description: string;
  confidence: number;
  color: string;
}

export default function Home() {
  const [reflection, setReflection] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [result, setResult] = useState<EmotionResponse | null>(null);
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!reflection.trim()) {
      setError('Please enter some text to analyze');
      return;
    }

    setIsLoading(true);
    setError(null);
    
    try {
      const response = await fetch('http://localhost:8000/analyze', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ text: reflection }),
      });

      if (!response.ok) {
        throw new Error('Failed to analyze emotion');
      }

      const data = await response.json();
      setResult(data);
    } catch (err) {
      console.error('Error analyzing emotion:', err);
      setError('Failed to analyze your reflection. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <main className="flex min-h-screen flex-col items-center p-6 sm:p-24">
      <div className="z-10 w-full max-w-md">
        <h1 className="text-3xl font-bold text-center mb-8">
          Emotion Reflection Tool
        </h1>
        
        <div className="bg-white rounded-lg shadow-lg p-6 w-full dark:bg-gray-800">
          <form onSubmit={handleSubmit}>
            <div className="mb-4">
              <label htmlFor="reflection" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                Share your thoughts or feelings
              </label>
              <textarea
                id="reflection"
                rows={4}
                className="w-full px-3 py-2 text-gray-700 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 dark:bg-gray-700 dark:text-white dark:border-gray-600"
                placeholder="Example: I feel nervous about my first job interview..."
                value={reflection}
                onChange={(e) => setReflection(e.target.value)}
              />
            </div>
            <button
              type="submit"
              disabled={isLoading}
              className={`w-full bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-4 rounded-lg ${
                isLoading ? 'opacity-50 cursor-not-allowed' : ''
              }`}
            >
              {isLoading ? 'Analyzing...' : 'Analyze My Emotion'}
            </button>
          </form>

          {error && (
            <div className="mt-4 p-3 bg-red-100 text-red-700 rounded-lg dark:bg-red-900 dark:text-red-300">
              {error}
            </div>
          )}

          {result && (
            <div 
              className="mt-6 p-4 rounded-lg shadow-md transition-all duration-300"
              style={{ backgroundColor: `${result.color}20`, borderLeft: `4px solid ${result.color}` }}
            >
              <div className="flex justify-between items-center mb-2">
                <h3 className="text-lg font-bold" style={{ color: result.color }}>
                  {result.emotion}
                </h3>
                <span className="text-sm text-gray-600 dark:text-gray-400">
                  {Math.round(result.confidence * 100)}% confidence
                </span>
              </div>
              <p className="text-gray-700 dark:text-gray-300">{result.description}</p>
              
              {/* Feedback buttons */}
              <div className="mt-4 flex items-center justify-between border-t pt-3 border-gray-200 dark:border-gray-700">
                <p className="text-sm text-gray-500">Was this analysis accurate?</p>
                <div className="flex gap-2">
                  <button 
                    onClick={() => alert("Thank you for your feedback!")}
                    className="text-xs bg-green-100 text-green-700 px-2 py-1 rounded hover:bg-green-200 dark:bg-green-900 dark:text-green-300"
                  >
                    üëç Yes
                  </button>
                  <button 
                    onClick={() => alert("We'll use your feedback to improve our analysis.")}
                    className="text-xs bg-red-100 text-red-700 px-2 py-1 rounded hover:bg-red-200 dark:bg-red-900 dark:text-red-300"
                  >
                    üëé No
                  </button>
                </div>
              </div>
            </div>
          )}
        </div>

        <p className="mt-6 text-center text-sm text-gray-500">
          This is a demo application. Emotion analysis is randomized.
        </p>
      </div>
    </main>
  );
}
